import React, { useState, useEffect } from 'react';
import { useToast } from '../contexts/ToastContext';
import '../App.css';

const HelpGuestBidding = ({ request, onClose, isChef = false }) => {
  const { showSuccess, showError } = useToast();
  const [bids, setBids] = useState([]);
  const [newBid, setNewBid] = useState({
    price: request?.budget || 0,
    message: '',
    estimatedTime: '',
    specialOffers: ''
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadBids();
  }, [request]);

  useEffect(() => {
    //      (  )
    if (!isChef && bids.length === 0 && request) {
      createDemoBids();
    }
  }, [isChef, request]);

  const createDemoBids = () => {
    const demoBids = [
      {
        id: `bid_${Date.now()}_1`,
        chefId: 'demo-chef-1',
        chefName: ' ',
        chefAvatar: '',
        price: 4500,
        message: '    10 .    !',
        estimatedTime: '3-4 ',
        specialOffers: '  ,   ',
        createdAt: new Date(Date.now() - 3600000).toISOString(), // 1  
        status: 'pending'
      },
      {
        id: `bid_${Date.now()}_2`,
        chefId: 'demo-chef-2',
        chefName: ' ',
        chefAvatar: '',
        price: 4200,
        message: '   .     !',
        estimatedTime: '2-3 ',
        specialOffers: ' 10%    5000',
        createdAt: new Date(Date.now() - 1800000).toISOString(), // 30  
        status: 'pending'
      }
    ];

    setBids(demoBids);
    saveBids(demoBids);
  };

  const loadBids = () => {
    try {
      const savedBids = JSON.parse(localStorage.getItem(`helpGuestBids_${request.id}`) || '[]');
      setBids(savedBids);
    } catch (error) {
      console.error('Error loading bids:', error);
    }
  };

  const saveBids = (updatedBids) => {
    try {
      localStorage.setItem(`helpGuestBids_${request.id}`, JSON.stringify(updatedBids));
    } catch (error) {
      console.error('Error saving bids:', error);
    }
  };

  const handleSubmitBid = () => {
    if (!newBid.price || newBid.price <= 0) {
      showError('  ');
      return;
    }

    setLoading(true);
    
    try {
      const bid = {
        id: `bid_${Date.now()}`,
        chefId: localStorage.getItem('chefId') || 'demo-chef-1',
        chefName: localStorage.getItem('chefName') || '',
        chefAvatar: localStorage.getItem('chefAvatar') || '',
        price: parseInt(newBid.price),
        message: newBid.message,
        estimatedTime: newBid.estimatedTime,
        specialOffers: newBid.specialOffers,
        createdAt: new Date().toISOString(),
        status: 'pending' // pending, accepted, rejected
      };

      const updatedBids = [...bids, bid];
      setBids(updatedBids);
      saveBids(updatedBids);

      //     
      const clientNotification = {
        id: `notification_${Date.now()}`,
        type: 'help_guest_bid',
        title: '   ',
        message: `${bid.chefName}   : ${bid.price}`,
        requestId: request.id,
        chefId: bid.chefId,
        chefName: bid.chefName,
        price: bid.price,
        createdAt: new Date().toISOString(),
        read: false
      };

      const clientNotifications = JSON.parse(localStorage.getItem('clientNotifications') || '[]');
      clientNotifications.unshift(clientNotification);
      localStorage.setItem('clientNotifications', JSON.stringify(clientNotifications));

      //  
      window.dispatchEvent(new CustomEvent('clientNotificationAdded', { 
        detail: clientNotification 
      }));

      setNewBid({
        price: request?.budget || 0,
        message: '',
        estimatedTime: '',
        specialOffers: ''
      });

      showSuccess('  ');
    } catch (error) {
      console.error('Error submitting bid:', error);
      showError('  ');
    } finally {
      setLoading(false);
    }
  };

  const handleAcceptBid = (bidId) => {
    try {
      const updatedBids = bids.map(bid => 
        bid.id === bidId 
          ? { ...bid, status: 'accepted' }
          : { ...bid, status: 'rejected' }
      );
      
      setBids(updatedBids);
      saveBids(updatedBids);

      //     
      const acceptedBid = updatedBids.find(bid => bid.id === bidId);
      const chefNotification = {
        id: `notification_${Date.now()}`,
        type: 'help_guest_bid_accepted',
        title: ' !',
        message: `     ${acceptedBid.price}`,
        requestId: request.id,
        clientId: request.clientId,
        price: acceptedBid.price,
        createdAt: new Date().toISOString(),
        read: false
      };

      const chefNotifications = JSON.parse(localStorage.getItem('chefNotifications') || '[]');
      chefNotifications.unshift(chefNotification);
      localStorage.setItem('chefNotifications', JSON.stringify(chefNotifications));

      window.dispatchEvent(new CustomEvent('chefNotificationAdded', { 
        detail: chefNotification 
      }));

      showSuccess(' !  ');
    } catch (error) {
      console.error('Error accepting bid:', error);
      showError('  ');
    }
  };

  const handleRejectBid = (bidId) => {
    try {
      const updatedBids = bids.map(bid => 
        bid.id === bidId 
          ? { ...bid, status: 'rejected' }
          : bid
      );
      
      setBids(updatedBids);
      saveBids(updatedBids);
      showSuccess(' ');
    } catch (error) {
      console.error('Error rejecting bid:', error);
      showError('  ');
    }
  };

  const formatTime = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div className="help-guest-bidding-overlay" onClick={onClose}>
      <div className="help-guest-bidding-modal" onClick={(e) => e.stopPropagation()}>
        <div className="help-guest-bidding-header">
          <h3>   </h3>
          <button onClick={onClose} className="back-button"></button>
        </div>

        <div className="help-guest-bidding-content">
          {/*    */}
          <div className="request-info">
            <h4>  </h4>
            <div className="request-details">
              <p><strong> :</strong> {request.budget}</p>
              <p><strong> :</strong> {new Date(request.eventDate).toLocaleDateString('ru-RU')}</p>
              <p><strong> :</strong> {request.guestCount}</p>
              <p><strong>:</strong> {request.address}</p>
              {request.specialRequests && (
                <p><strong> :</strong> {request.specialRequests}</p>
              )}
            </div>
          </div>

          {/*    ( ) */}
          {isChef && (
            <div className="bid-form">
              <h4>  </h4>
              <div className="form-group">
                <label>  ():</label>
                <input
                  type="number"
                  value={newBid.price}
                  onChange={(e) => setNewBid(prev => ({ ...prev, price: e.target.value }))}
                  min="1"
                  placeholder="  "
                />
              </div>
              
              <div className="form-group">
                <label> :</label>
                <input
                  type="text"
                  value={newBid.estimatedTime}
                  onChange={(e) => setNewBid(prev => ({ ...prev, estimatedTime: e.target.value }))}
                  placeholder=": 2-3 "
                />
              </div>
              
              <div className="form-group">
                <label> :</label>
                <textarea
                  value={newBid.message}
                  onChange={(e) => setNewBid(prev => ({ ...prev, message: e.target.value }))}
                  placeholder="     ..."
                  rows={3}
                />
              </div>
              
              <div className="form-group">
                <label> :</label>
                <textarea
                  value={newBid.specialOffers}
                  onChange={(e) => setNewBid(prev => ({ ...prev, specialOffers: e.target.value }))}
                  placeholder=" , ..."
                  rows={2}
                />
              </div>
              
              <button 
                onClick={handleSubmitBid} 
                className="submit-bid-btn"
                disabled={loading}
              >
                {loading ? '...' : '  '}
              </button>
            </div>
          )}

          {/*   */}
          <div className="bids-list">
            <h4>  </h4>
            {bids.length === 0 ? (
              <div className="no-bids">
                <p>    </p>
              </div>
            ) : (
              <div className="bids-container">
                {bids.map(bid => (
                  <div key={bid.id} className={`bid-card ${bid.status}`}>
                    <div className="bid-header">
                      <div className="chef-info">
                        <span className="chef-avatar">{bid.chefAvatar}</span>
                        <div>
                          <h5>{bid.chefName}</h5>
                          <p className="bid-time">{formatTime(bid.createdAt)}</p>
                        </div>
                      </div>
                      <div className="bid-price">
                        <span className="price">{bid.price}</span>
                        <span className={`status ${bid.status}`}>
                          {bid.status === 'pending' && ' '}
                          {bid.status === 'accepted' && ' '}
                          {bid.status === 'rejected' && ' '}
                        </span>
                      </div>
                    </div>
                    
                    <div className="bid-details">
                      {bid.estimatedTime && (
                        <p><strong>:</strong> {bid.estimatedTime}</p>
                      )}
                      {bid.message && (
                        <p><strong>:</strong> {bid.message}</p>
                      )}
                      {bid.specialOffers && (
                        <p><strong>:</strong> {bid.specialOffers}</p>
                      )}
                    </div>
                    
                    {!isChef && bid.status === 'pending' && (
                      <div className="bid-actions">
                        <button 
                          onClick={() => handleAcceptBid(bid.id)}
                          className="accept-btn"
                        >
                           
                        </button>
                        <button 
                          onClick={() => handleRejectBid(bid.id)}
                          className="reject-btn"
                        >
                           
                        </button>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default HelpGuestBidding;
