import React, { useState, useEffect } from 'react';

const DiabeticMenuSection = ({ dishes = [] }) => {
  const [filteredDishes, setFilteredDishes] = useState([]);
  const [sortBy, setSortBy] = useState('gi'); // gi, calories, protein
  const [filterBy, setFilterBy] = useState('all'); // all, low-gi, high-protein, low-calories

  useEffect(() => {
    let filtered = dishes.filter(dish => dish.diabeticFriendly === true);
    
    //  
    switch (filterBy) {
      case 'low-gi':
        filtered = filtered.filter(dish => dish.glycemicIndex < 50);
        break;
      case 'high-protein':
        filtered = filtered.filter(dish => dish.protein > 15);
        break;
      case 'low-calories':
        filtered = filtered.filter(dish => dish.calories < 300);
        break;
      default:
        break;
    }

    // 
    switch (sortBy) {
      case 'gi':
        filtered.sort((a, b) => (a.glycemicIndex || 0) - (b.glycemicIndex || 0));
        break;
      case 'calories':
        filtered.sort((a, b) => (a.calories || 0) - (b.calories || 0));
        break;
      case 'protein':
        filtered.sort((a, b) => (b.protein || 0) - (a.protein || 0));
        break;
      default:
        break;
    }

    setFilteredDishes(filtered);
  }, [dishes, sortBy, filterBy]);

  const getGIColor = (gi) => {
    if (gi < 50) return '#4caf50'; //  -  
    if (gi < 70) return '#ff9800'; //  -  
    return '#f44336'; //  -  
  };

  const getGILabel = (gi) => {
    if (gi < 50) return ' ';
    if (gi < 70) return ' ';
    return ' ';
  };

  return (
    <div style={{ padding: '20px' }}>
      {/*   */}
      <div style={{
        textAlign: 'center',
        marginBottom: '30px',
        padding: '20px',
        backgroundColor: '#e8f5e8',
        borderRadius: '12px',
        border: '2px solid #4caf50'
      }}>
        <div style={{ fontSize: '24px', fontWeight: 'bold', color: '#2e7d32', marginBottom: '10px' }}>
             
        </div>
        <div style={{ fontSize: '14px', color: '#666' }}>
             AI-      
        </div>
        <div style={{ fontSize: '12px', color: '#4caf50', marginTop: '5px' }}>
                    
        </div>
      </div>

      {/*    */}
      <div style={{
        display: 'flex',
        gap: '15px',
        marginBottom: '20px',
        flexWrap: 'wrap',
        alignItems: 'center'
      }}>
        <div>
          <label style={{ marginRight: '5px', fontWeight: 'bold' }}>:</label>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            style={{
              padding: '5px 10px',
              borderRadius: '4px',
              border: '1px solid #ddd'
            }}
          >
            <option value="gi">  </option>
            <option value="calories"> </option>
            <option value="protein"> </option>
          </select>
        </div>

        <div>
          <label style={{ marginRight: '5px', fontWeight: 'bold' }}>:</label>
          <select
            value={filterBy}
            onChange={(e) => setFilterBy(e.target.value)}
            style={{
              padding: '5px 10px',
              borderRadius: '4px',
              border: '1px solid #ddd'
            }}
          >
            <option value="all"> </option>
            <option value="low-gi">  (&lt;50)</option>
            <option value="high-protein"> (&gt;15)</option>
            <option value="low-calories"> (&lt;300 )</option>
          </select>
        </div>

        <div style={{
          fontSize: '12px',
          color: '#666',
          marginLeft: 'auto'
        }}>
          : {filteredDishes.length} 
        </div>
      </div>

      {/*   */}
      {filteredDishes.length === 0 ? (
        <div style={{
          textAlign: 'center',
          padding: '40px',
          color: '#666',
          fontSize: '16px'
        }}>
          <div style={{ fontSize: '48px', marginBottom: '10px' }}></div>
          <div>    </div>
          <div style={{ fontSize: '12px', marginTop: '5px' }}>
                 
          </div>
        </div>
      ) : (
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
          gap: '20px'
        }}>
          {filteredDishes.map((dish, index) => (
            <div key={index} style={{
              border: '2px solid #4caf50',
              borderRadius: '12px',
              padding: '15px',
              backgroundColor: '#f8fff8',
              transition: 'transform 0.2s ease',
              cursor: 'pointer'
            }}
            onMouseEnter={(e) => e.target.style.transform = 'translateY(-2px)'}
            onMouseLeave={(e) => e.target.style.transform = 'translateY(0)'}
            >
              {/*   */}
              <div style={{
                fontSize: '18px',
                fontWeight: 'bold',
                marginBottom: '10px',
                color: '#2e7d32'
              }}>
                {dish.name}
              </div>

              {/*   */}
              {dish.image && (
                <div style={{
                  width: '100%',
                  height: '150px',
                  backgroundColor: '#f0f0f0',
                  borderRadius: '8px',
                  marginBottom: '10px',
                  backgroundImage: `url(${dish.image})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center'
                }} />
              )}

              {/*   */}
              <div style={{
                display: 'grid',
                gridTemplateColumns: '1fr 1fr',
                gap: '8px',
                marginBottom: '10px',
                fontSize: '12px'
              }}>
                <div>
                  <span style={{ fontWeight: 'bold' }}>:</span> {dish.calories || 0} 
                </div>
                <div>
                  <span style={{ fontWeight: 'bold' }}>:</span> {dish.protein || 0}
                </div>
                <div>
                  <span style={{ fontWeight: 'bold' }}>:</span> {dish.carbs || 0}
                </div>
                <div>
                  <span style={{ fontWeight: 'bold' }}>:</span> {dish.fat || 0}
                </div>
              </div>

              {/*   */}
              <div style={{
                display: 'flex',
                alignItems: 'center',
                marginBottom: '10px',
                padding: '5px 10px',
                backgroundColor: getGIColor(dish.glycemicIndex || 0),
                borderRadius: '15px',
                color: 'white',
                fontSize: '12px',
                fontWeight: 'bold'
              }}>
                <span style={{ marginRight: '5px' }}></span>
                : {dish.glycemicIndex || 0} ({getGILabel(dish.glycemicIndex || 0)})
              </div>

              {/*  */}
              <div style={{
                display: 'flex',
                flexWrap: 'wrap',
                gap: '5px',
                marginBottom: '10px'
              }}>
                <span style={{
                  padding: '2px 8px',
                  backgroundColor: '#4caf50',
                  color: 'white',
                  borderRadius: '10px',
                  fontSize: '10px',
                  fontWeight: 'bold'
                }}>
                    
                </span>
                <span style={{
                  padding: '2px 8px',
                  backgroundColor: '#2196f3',
                  color: 'white',
                  borderRadius: '10px',
                  fontSize: '10px',
                  fontWeight: 'bold'
                }}>
                   
                </span>
                {dish.halal && (
                  <span style={{
                    padding: '2px 8px',
                    backgroundColor: '#9c27b0',
                    color: 'white',
                    borderRadius: '10px',
                    fontSize: '10px',
                    fontWeight: 'bold'
                  }}>
                     
                  </span>
                )}
              </div>

              {/*  */}
              {dish.description && (
                <div style={{
                  fontSize: '12px',
                  color: '#666',
                  marginBottom: '10px',
                  lineHeight: '1.4'
                }}>
                  {dish.description}
                </div>
              )}

              {/* AI- */}
              <div style={{
                fontSize: '11px',
                color: '#1976d2',
                backgroundColor: '#e3f2fd',
                padding: '8px',
                borderRadius: '6px',
                borderLeft: '3px solid #2196f3'
              }}>
                <div style={{ fontWeight: 'bold', marginBottom: '3px' }}> AI-:</div>
                <div>
                  {dish.glycemicIndex < 50 
                    ? '  !     .'
                    : dish.glycemicIndex < 70
                    ? '     .'
                    : '   .'
                  }
                </div>
              </div>

              {/*   */}
              <button style={{
                width: '100%',
                padding: '10px',
                backgroundColor: '#4caf50',
                color: 'white',
                border: 'none',
                borderRadius: '6px',
                fontSize: '14px',
                fontWeight: 'bold',
                cursor: 'pointer',
                marginTop: '10px',
                transition: 'background-color 0.2s ease'
              }}
              onMouseEnter={(e) => e.target.style.backgroundColor = '#45a049'}
              onMouseLeave={(e) => e.target.style.backgroundColor = '#4caf50'}
              >
                   {dish.price || '0'}
              </button>
            </div>
          ))}
        </div>
      )}

      {/*   */}
      <div style={{
        marginTop: '30px',
        padding: '20px',
        backgroundColor: '#f5f5f5',
        borderRadius: '8px',
        fontSize: '12px',
        color: '#666'
      }}>
        <div style={{ fontWeight: 'bold', marginBottom: '10px', color: '#333' }}>
             
        </div>
        <div style={{ lineHeight: '1.5' }}>
                 AI-       .
             ,      .
                 ,     .
        </div>
      </div>
    </div>
  );
};

export default DiabeticMenuSection;
