//     
export const findProductInDatabase = (productName) => {
  const normalizedName = productName.toLowerCase().trim();
  
  //    
  for (const category in calorizatorInspiredDatabase) {
    const products = calorizatorInspiredDatabase[category];
    for (const product in products) {
      if (product.toLowerCase().includes(normalizedName) || 
          normalizedName.includes(product.toLowerCase())) {
        return {
          ...products[product],
          name: product,
          category: category
        };
      }
    }
  }
  
  return null;
};

//     
export const getProductsByCategory = (category) => {
  return calorizatorInspiredDatabase[category] || {};
};

//     
export const searchProducts = (query) => {
  const results = [];
  const normalizedQuery = query.toLowerCase().trim();
  
  for (const category in calorizatorInspiredDatabase) {
    const products = calorizatorInspiredDatabase[category];
    for (const product in products) {
      if (product.toLowerCase().includes(normalizedQuery)) {
        results.push({
          name: product,
          ...products[product],
          category: category
        });
      }
    }
  }
  
  return results;
};

//     
export const getDatabaseStatistics = () => {
  let totalProducts = 0;
  const categoryStats = {};
  
  for (const category in calorizatorInspiredDatabase) {
    const count = Object.keys(calorizatorInspiredDatabase[category]).length;
    categoryStats[category] = count;
    totalProducts += count;
  }
  
  return {
    totalProducts,
    categoryStats,
    categories: Object.keys(calorizatorInspiredDatabase)
  };
};

//    
export const addProductToDatabase = (category, productName, nutritionData) => {
  if (!calorizatorInspiredDatabase[category]) {
    calorizatorInspiredDatabase[category] = {};
  }
  
  calorizatorInspiredDatabase[category][productName] = {
    ...nutritionData,
    category: category
  };
};

//   
export const updateProductInDatabase = (category, productName, nutritionData) => {
  if (calorizatorInspiredDatabase[category] && calorizatorInspiredDatabase[category][productName]) {
    calorizatorInspiredDatabase[category][productName] = {
      ...calorizatorInspiredDatabase[category][productName],
      ...nutritionData
    };
  }
};

//    
export const exportDatabase = () => {
  return {
    database: calorizatorInspiredDatabase,
    statistics: getDatabaseStatistics(),
    exportDate: new Date().toISOString(),
    source: 'Calorizator.ru inspired database'
  };
};
