import React, { useState } from 'react';

const HelpGuestRequest = ({ dish, onClose, onRequestSubmit }) => {
  const [formData, setFormData] = useState({
    eventDate: '',
    eventTime: '',
    numberOfGuests: '',
    eventType: '',
    budget: '',
    specialRequests: '',
    contactPhone: '',
    address: '',
    dietaryRestrictions: '',
    preferredCuisine: '',
    servingStyle: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    try {
      const requestData = {
        dishId: dish.id,
        chefId: dish.chefId,
        ...formData,
        type: 'help_guest',
        estimatedTime: dish.estimatedTime,
        minOrderValue: dish.minOrderValue,
        maxGuests: dish.maxGuests,
        includesPlanning: dish.includesPlanning,
        includesServing: dish.includesServing,
        includesCleanup: dish.includesCleanup,
        isExpress: dish.isExpress,
        isFullService: dish.isFullService
      };
      
      await onRequestSubmit(requestData);
      onClose();
    } catch (error) {
      console.error('Error submitting help guest request:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="help-guest-modal">
      <div className="help-guest-overlay" onClick={onClose}>
        <div className="help-guest-content" onClick={(e) => e.stopPropagation()}>
          <div className="help-guest-header">
            <h2> {dish.name}</h2>
            <button className="close-button" onClick={onClose}></button>
          </div>
          
          <div className="help-guest-body">
            <p className="dish-description">{dish.description}</p>
            
            <form onSubmit={handleSubmit} className="help-guest-form">
              {/*     */}
              <div className="form-section">
                <h3>   </h3>
                
                <div className="form-row">
                  <div className="form-group">
                    <label>  </label>
                    <input
                      type="date"
                      name="eventDate"
                      value={formData.eventDate}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  
                  <div className="form-group">
                    <label>  </label>
                    <input
                      type="time"
                      name="eventTime"
                      value={formData.eventTime}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>

                <div className="form-row">
                  <div className="form-group">
                    <label>  </label>
                    <input
                      type="number"
                      name="numberOfGuests"
                      value={formData.numberOfGuests}
                      onChange={handleInputChange}
                      min="1"
                      max={dish.maxGuests || 50}
                      required
                    />
                    <small>: {dish.maxGuests || 50} </small>
                  </div>
                  
                  <div className="form-group">
                    <label>  </label>
                    <select
                      name="eventType"
                      value={formData.eventType}
                      onChange={handleInputChange}
                      required
                    >
                      <option value=""> </option>
                      <option value="birthday"> </option>
                      <option value="anniversary"></option>
                      <option value="wedding"></option>
                      <option value="corporate"></option>
                      <option value="family"> </option>
                      <option value="holiday"></option>
                      <option value="other"></option>
                    </select>
                  </div>
                </div>
              </div>

              {/*   */}
              <div className="form-section">
                <h3>  </h3>
                
                <div className="form-group">
                  <label>  </label>
                  <select
                    name="preferredCuisine"
                    value={formData.preferredCuisine}
                    onChange={handleInputChange}
                  >
                    <option value=""></option>
                    <option value="russian"></option>
                    <option value="tatar"></option>
                    <option value="european"></option>
                    <option value="asian"></option>
                    <option value="mediterranean"></option>
                    <option value="american"></option>
                    <option value="mixed"></option>
                  </select>
                </div>

                <div className="form-group">
                  <label>  </label>
                  <select
                    name="servingStyle"
                    value={formData.servingStyle}
                    onChange={handleInputChange}
                  >
                    <option value=""> </option>
                    <option value="buffet"></option>
                    <option value="sit-down"> </option>
                    <option value="family-style"> </option>
                    <option value="cocktail"> </option>
                    <option value="bbq"></option>
                  </select>
                </div>

                <div className="form-group">
                  <label>  </label>
                  <textarea
                    name="dietaryRestrictions"
                    value={formData.dietaryRestrictions}
                    onChange={handleInputChange}
                    placeholder=", , , ,   .."
                    rows="3"
                  />
                </div>
              </div>

              {/*     */}
              <div className="form-section">
                <h3>   </h3>
                
                <div className="form-group">
                  <label>  (.)</label>
                  <input
                    type="number"
                    name="budget"
                    value={formData.budget}
                    onChange={handleInputChange}
                    placeholder={` ${dish.minOrderValue} .`}
                    min={dish.minOrderValue}
                    required
                  />
                  <small> : {dish.minOrderValue} .</small>
                </div>

                <div className="form-group">
                  <label>  </label>
                  <textarea
                    name="specialRequests"
                    value={formData.specialRequests}
                    onChange={handleInputChange}
                    placeholder="   , , ..."
                    rows="4"
                  />
                </div>
              </div>

              {/*   */}
              <div className="form-section">
                <h3>  </h3>
                
                <div className="form-group">
                  <label>   </label>
                  <input
                    type="tel"
                    name="contactPhone"
                    value={formData.contactPhone}
                    onChange={handleInputChange}
                    placeholder="+7 (999) 123-45-67"
                    required
                  />
                </div>

                <div className="form-group">
                  <label>  </label>
                  <input
                    type="text"
                    name="address"
                    value={formData.address}
                    onChange={handleInputChange}
                    placeholder=", , "
                    required
                  />
                </div>
              </div>

              {/*    */}
              <div className="chef-info">
                <h4> : {dish.chef}</h4>
                <p> : {dish.rating}/5</p>
                <p>  : {dish.estimatedTime}</p>
                {dish.includesPlanning && <p>   </p>}
                {dish.includesServing && <p>  </p>}
                {dish.includesCleanup && <p>  </p>}
                {dish.isExpress && <p> -</p>}
                {dish.isFullService && <p>  </p>}
              </div>

              <div className="form-actions">
                <button type="button" onClick={onClose} className="cancel-btn">
                  
                </button>
                <button type="submit" disabled={isSubmitting} className="submit-btn">
                  {isSubmitting ? '...' : ' '}
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HelpGuestRequest;
