import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { safeSetClientOrders } from '../utils/safeStorage';

const AdminOrderDetails = () => {
  const [order, setOrder] = useState(null);
  const [chef, setChef] = useState(null);
  const [customer, setCustomer] = useState(null);
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState(false);
  const [message, setMessage] = useState('');
  const navigate = useNavigate();
  const { orderId } = useParams();

  const loadOrderDetails = useCallback(() => {
    setLoading(true);
    
    //  
    const orders = JSON.parse(localStorage.getItem('clientOrders') || '[]');
    const foundOrder = orders.find(o => o.id === orderId);
    
    if (!foundOrder) {
      setMessage('  ');
      setLoading(false);
      return;
    }
    
    setOrder(foundOrder);
    
    //     
    const users = JSON.parse(localStorage.getItem('allUsers') || '[]');
    const foundChef = users.find(u => u.email === foundOrder.chefId);
    const foundCustomer = users.find(u => u.email === foundOrder.customer?.email);
    
    setChef(foundChef);
    setCustomer(foundCustomer);
    setLoading(false);
  }, [orderId]);

  useEffect(() => {
    //  
    const token = localStorage.getItem('authToken');
    const role = localStorage.getItem('role');
    
    if (!token || role !== 'admin') {
      navigate('/admin/login');
      return;
    }

    loadOrderDetails();
  }, [navigate, orderId, loadOrderDetails]);

  const createChefNotification = (order, status) => {
    try {
      const notifications = JSON.parse(localStorage.getItem('chefNotifications') || '[]');
      
      let notification = null;
      const chefId = order.items && order.items.length > 0 ? order.items[0].chefId : 'unknown';
      
      switch (status) {
        case 'confirmed':
          notification = {
            id: `notification-${Date.now()}`,
            type: 'orderConfirmed',
            title: ' ',
            message: ` #${order.id}     `,
            time: new Date(),
            read: false,
            icon: '',
            orderId: order.id,
            chefId: chefId
          };
          break;
        case 'preparing':
          notification = {
            id: `notification-${Date.now()}`,
            type: 'orderPreparing',
            title: ' ',
            message: ` #${order.id}   `,
            time: new Date(),
            read: false,
            icon: '',
            orderId: order.id,
            chefId: chefId
          };
          break;
        case 'delivering':
          notification = {
            id: `notification-${Date.now()}`,
            type: 'orderDelivering',
            title: '  ',
            message: ` #${order.id}  `,
            time: new Date(),
            read: false,
            icon: '',
            orderId: order.id,
            chefId: chefId
          };
          break;
        case 'delivered':
          notification = {
            id: `notification-${Date.now()}`,
            type: 'orderDelivered',
            title: ' ',
            message: ` #${order.id}   `,
            time: new Date(),
            read: false,
            icon: '',
            orderId: order.id,
            chefId: chefId
          };
          break;
        case 'cancelled':
          notification = {
            id: `notification-${Date.now()}`,
            type: 'orderCancelled',
            title: ' ',
            message: ` #${order.id}  `,
            time: new Date(),
            read: false,
            icon: '',
            orderId: order.id,
            chefId: chefId
          };
          break;
        default:
          //      
          break;
      }
      
      if (notification) {
        notifications.unshift(notification);
        const limitedNotifications = notifications.slice(0, 50);
        localStorage.setItem('chefNotifications', JSON.stringify(limitedNotifications));
        console.log('Created chef notification:', notification);
      }
    } catch (error) {
      console.error('Error creating chef notification:', error);
    }
  };

  const updateOrderStatus = async (newStatus) => {
    setUpdating(true);
    setMessage('');
    
    try {
      const orders = JSON.parse(localStorage.getItem('clientOrders') || '[]');
      const updatedOrders = orders.map(o => 
        o.id === orderId 
          ? { ...o, status: newStatus, updatedAt: new Date().toISOString() }
          : o
      );
      
      safeSetClientOrders(updatedOrders);
      setOrder(prev => ({ ...prev, status: newStatus, updatedAt: new Date().toISOString() }));
      
      //    
      createChefNotification(order, newStatus);
      
      setMessage('  !');
      setTimeout(() => setMessage(''), 3000);
      
    } catch (error) {
      setMessage('   ');
    } finally {
      setUpdating(false);
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      'pending': '#ff9800',
      'confirmed': '#2196f3',
      'preparing': '#9c27b0',
      'delivering': '#3f51b5',
      'delivered': '#4caf50',
      'cancelled': '#f44336'
    };
    return colors[status] || '#666';
  };

  const getStatusText = (status) => {
    const statusMap = {
      'pending': ' ',
      'confirmed': '',
      'preparing': '',
      'delivering': ' ',
      'delivered': '',
      'cancelled': ''
    };
    return statusMap[status] || status;
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const formatCurrency = (amount) => {
    return amount.toLocaleString('ru-RU') + ' ';
  };

  if (loading) {
    return (
      <div className="admin-order-details">
        <div className="loading">  ...</div>
      </div>
    );
  }

  if (!order) {
    return (
      <div className="admin-order-details">
        <div className="error-message">
          <h2>   </h2>
          <p>  ID {orderId}  </p>
          <button onClick={() => navigate('/admin/orders')} className="back-button">
               
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="admin-order-details">
      <div className="page-header">
        <button 
          onClick={() => navigate('/admin/orders')} 
          className="back-button"
        >
             
        </button>
        <h1>   #{order.id}</h1>
        <div className="order-status">
          <span 
            className="status-badge"
            style={{ backgroundColor: getStatusColor(order.status) }}
          >
            {getStatusText(order.status)}
          </span>
        </div>
      </div>

      {message && (
        <div className={`message ${message.includes('') ? 'error' : 'success'}`}>
          {message}
        </div>
      )}

      <div className="order-content">
        {/*    */}
        <div className="order-section">
          <h2>   </h2>
          <div className="order-info-grid">
            <div className="info-item">
              <span className="label">ID :</span>
              <span className="value">#{order.id}</span>
            </div>
            <div className="info-item">
              <span className="label"> :</span>
              <span className="value">{formatDate(order.createdAt)}</span>
            </div>
            <div className="info-item">
              <span className="label"> :</span>
              <span className="value">{formatDate(order.updatedAt || order.createdAt)}</span>
            </div>
            <div className="info-item">
              <span className="label"> :</span>
              <span className="value">{formatCurrency(order.payment?.total || 0)}</span>
            </div>
            <div className="info-item">
              <span className="label">:</span>
              <span className="value">{formatCurrency(order.payment?.commission || 0)}</span>
            </div>
            <div className="info-item">
              <span className="label"> :</span>
              <span className="value">{formatCurrency((order.payment?.total || 0) - (order.payment?.commission || 0))}</span>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="order-section">
          <h2>   </h2>
          <div className="customer-info">
            <div className="info-item">
              <span className="label">:</span>
              <span className="value">{customer?.name || order.customer?.name || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label">Email:</span>
              <span className="value">{customer?.email || order.customer?.email || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label">:</span>
              <span className="value">{customer?.phone || order.customer?.phone || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label"> :</span>
              <span className="value">{order.delivery?.address || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label">  :</span>
              <span className="value">{order.delivery?.comment || ' '}</span>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="order-section">
          <h2>   </h2>
          <div className="chef-info">
            <div className="info-item">
              <span className="label">:</span>
              <span className="value">{chef?.name || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label">Email:</span>
              <span className="value">{chef?.email || order.chefId || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label">:</span>
              <span className="value">{chef?.specialization || ' '}</span>
            </div>
            <div className="info-item">
              <span className="label">:</span>
              <span className="value">{chef?.experience || ' '} </span>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="order-section">
          <h2>   </h2>
          <div className="dishes-list">
            {order.items?.map((item, index) => (
              <div key={index} className="dish-item">
                <div className="dish-image">
                  {item.image ? (
                    <img src={item.image} alt={item.name} />
                  ) : (
                    <div className="no-image"></div>
                  )}
                </div>
                <div className="dish-info">
                  <h4>{item.name}</h4>
                  <p className="dish-description">{item.description}</p>
                  <div className="dish-details">
                    <span className="quantity">: {item.quantity}</span>
                    <span className="price">{formatCurrency(item.price)}</span>
                    <span className="total">{formatCurrency(item.price * item.quantity)}</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/*   */}
        <div className="order-section">
          <h2>  </h2>
          <div className="status-controls">
            <div className="current-status">
              <span className="label"> :</span>
              <span 
                className="status-badge"
                style={{ backgroundColor: getStatusColor(order.status) }}
              >
                {getStatusText(order.status)}
              </span>
            </div>
            
            <div className="status-buttons">
              {order.status === 'pending' && (
                <button 
                  onClick={() => updateOrderStatus('confirmed')}
                  className="status-button confirm"
                  disabled={updating}
                >
                    
                </button>
              )}
              
              {order.status === 'confirmed' && (
                <button 
                  onClick={() => updateOrderStatus('preparing')}
                  className="status-button preparing"
                  disabled={updating}
                >
                    
                </button>
              )}
              
              {order.status === 'preparing' && (
                <button 
                  onClick={() => updateOrderStatus('delivering')}
                  className="status-button delivering"
                  disabled={updating}
                >
                    
                </button>
              )}
              
              {order.status === 'delivering' && (
                <button 
                  onClick={() => updateOrderStatus('delivered')}
                  className="status-button delivered"
                  disabled={updating}
                >
                    
                </button>
              )}
              
              {!['delivered', 'cancelled'].includes(order.status) && (
                <button 
                  onClick={() => updateOrderStatus('cancelled')}
                  className="status-button cancel"
                  disabled={updating}
                >
                    
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminOrderDetails;
