import React, { useState } from 'react';

const MasterClassRequest = ({ dish, onClose, onRequestSubmit }) => {
  const [formData, setFormData] = useState({
    preferredDate: '',
    preferredTime: '',
    numberOfParticipants: '',
    skillLevel: '',
    dietaryRestrictions: '',
    specialRequests: '',
    contactPhone: '',
    contactEmail: '',
    address: '',
    groupType: '',
    ageGroup: '',
    languagePreference: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    try {
      const requestData = {
        dishId: dish.id,
        chefId: dish.chefId,
        ...formData,
        type: 'master_class',
        estimatedTime: dish.estimatedTime,
        minOrderValue: dish.minOrderValue,
        maxParticipants: dish.maxParticipants,
        difficulty: dish.difficulty,
        cuisine: dish.cuisine,
        includesIngredients: dish.includesIngredients,
        includesRecipe: dish.includesRecipe,
        includesCertificate: dish.includesCertificate,
        isBaking: dish.isBaking,
        isBasic: dish.isBasic,
        isEastern: dish.isEastern
      };
      
      await onRequestSubmit(requestData);
      onClose();
    } catch (error) {
      console.error('Error submitting master class request:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="master-class-modal">
      <div className="master-class-overlay" onClick={onClose}>
        <div className="master-class-content" onClick={(e) => e.stopPropagation()}>
          <div className="master-class-header">
            <h2> {dish.name}</h2>
            <button className="close-button" onClick={onClose}></button>
          </div>
          
          <div className="master-class-body">
            <p className="dish-description">{dish.description}</p>
            
            <form onSubmit={handleSubmit} className="master-class-form">
              {/*    - */}
              <div className="form-section">
                <h3>   -</h3>
                
                <div className="form-row">
                  <div className="form-group">
                    <label>  </label>
                    <input
                      type="date"
                      name="preferredDate"
                      value={formData.preferredDate}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                  
                  <div className="form-group">
                    <label>  </label>
                    <input
                      type="time"
                      name="preferredTime"
                      value={formData.preferredTime}
                      onChange={handleInputChange}
                      required
                    />
                  </div>
                </div>

                <div className="form-row">
                  <div className="form-group">
                    <label>  </label>
                    <input
                      type="number"
                      name="numberOfParticipants"
                      value={formData.numberOfParticipants}
                      onChange={handleInputChange}
                      min="1"
                      max={dish.maxParticipants || 20}
                      required
                    />
                    <small>: {dish.maxParticipants || 20} </small>
                  </div>
                  
                  <div className="form-group">
                    <label>  </label>
                    <select
                      name="skillLevel"
                      value={formData.skillLevel}
                      onChange={handleInputChange}
                      required
                    >
                      <option value=""> </option>
                      <option value="beginner"></option>
                      <option value="intermediate"></option>
                      <option value="advanced"></option>
                      <option value="mixed"></option>
                    </select>
                  </div>
                </div>
              </div>

              {/*   */}
              <div className="form-section">
                <h3>  </h3>
                
                <div className="form-row">
                  <div className="form-group">
                    <label>  </label>
                    <select
                      name="groupType"
                      value={formData.groupType}
                      onChange={handleInputChange}
                    >
                      <option value=""> </option>
                      <option value="friends"></option>
                      <option value="family"></option>
                      <option value="couple"></option>
                      <option value="corporate"></option>
                      <option value="individual"></option>
                      <option value="children"> </option>
                    </select>
                  </div>
                  
                  <div className="form-group">
                    <label>  </label>
                    <select
                      name="ageGroup"
                      value={formData.ageGroup}
                      onChange={handleInputChange}
                    >
                      <option value=""> </option>
                      <option value="children"> (6-12 )</option>
                      <option value="teens"> (13-17 )</option>
                      <option value="adults"> (18+ )</option>
                      <option value="seniors"> (60+ )</option>
                      <option value="mixed"> </option>
                    </select>
                  </div>
                </div>

                <div className="form-group">
                  <label>  </label>
                  <select
                    name="languagePreference"
                    value={formData.languagePreference}
                    onChange={handleInputChange}
                  >
                    <option value=""> </option>
                    <option value="russian"></option>
                    <option value="tatar"></option>
                    <option value="english"></option>
                    <option value="mixed"></option>
                  </select>
                </div>
              </div>

              {/*   */}
              <div className="form-section">
                <h3>  </h3>
                
                <div className="form-group">
                  <label>  </label>
                  <textarea
                    name="dietaryRestrictions"
                    value={formData.dietaryRestrictions}
                    onChange={handleInputChange}
                    placeholder=", , , ,   .."
                    rows="3"
                  />
                </div>

                <div className="form-group">
                  <label>  </label>
                  <textarea
                    name="specialRequests"
                    value={formData.specialRequests}
                    onChange={handleInputChange}
                    placeholder="   -, ,   ..."
                    rows="4"
                  />
                </div>
              </div>

              {/*   */}
              <div className="form-section">
                <h3>  </h3>
                
                <div className="form-row">
                  <div className="form-group">
                    <label>   </label>
                    <input
                      type="tel"
                      name="contactPhone"
                      value={formData.contactPhone}
                      onChange={handleInputChange}
                      placeholder="+7 (999) 123-45-67"
                      required
                    />
                  </div>
                  
                  <div className="form-group">
                    <label> Email</label>
                    <input
                      type="email"
                      name="contactEmail"
                      value={formData.contactEmail}
                      onChange={handleInputChange}
                      placeholder="example@email.com"
                      required
                    />
                  </div>
                </div>

                <div className="form-group">
                  <label>  </label>
                  <input
                    type="text"
                    name="address"
                    value={formData.address}
                    onChange={handleInputChange}
                    placeholder=", ,    "
                    required
                  />
                </div>
              </div>

              {/*   - */}
              <div className="chef-info">
                <h4> -: {dish.name}</h4>
                <p> : {dish.rating}/5</p>
                <p> : {dish.estimatedTime}</p>
                <p> : {dish.difficulty}</p>
                <p> : {dish.cuisine}</p>
                <p>  : {dish.maxParticipants}</p>
                <p> :  {dish.minOrderValue} .</p>
                {dish.includesIngredients && <p>   </p>}
                {dish.includesRecipe && <p>  </p>}
                {dish.includesCertificate && <p>  </p>}
                {dish.isBaking && <p> : </p>}
                {dish.isBasic && <p> :  </p>}
                {dish.isEastern && <p> :  </p>}
              </div>

              <div className="form-actions">
                <button type="button" onClick={onClose} className="cancel-btn">
                  
                </button>
                <button type="submit" disabled={isSubmitting} className="submit-btn">
                  {isSubmitting ? '...' : '  -'}
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MasterClassRequest;
