import React, { useState, useEffect, useCallback } from 'react';
import QRCode from 'qrcode';
import { useParams, useNavigate } from 'react-router-dom';
import Rating from './Rating';

const DishPassport = () => {
  const { dishId, chefId } = useParams();
  const navigate = useNavigate();
  const [dish, setDish] = useState(null);
  const [chef, setChef] = useState(null);
  const [qrCode, setQrCode] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [chefRating, setChefRating] = useState(0);
  const [chefReviewsCount, setChefReviewsCount] = useState(0);
  const [dishRating, setDishRating] = useState(0);
  const [dishReviewsCount, setDishReviewsCount] = useState(0);
  const [reviews, setReviews] = useState([]);

  const loadDishData = useCallback(async () => {
    try {
      setLoading(true);
      
      //   
      const dishes = JSON.parse(localStorage.getItem(`demo_menu_${chefId}`) || '[]');
      const foundDish = dishes.find(d => d.id === dishId);
      
      if (foundDish) {
        setDish(foundDish);
        
        //  QR-
        const qrData = JSON.stringify({
          dishId: foundDish.id,
          name: foundDish.name,
          chefId: chefId,
          timestamp: new Date().toISOString()
        });
        
        const qrCodeUrl = await QRCode.toDataURL(qrData);
        setQrCode(qrCodeUrl);
        
        //   (-)
        const reviews = JSON.parse(localStorage.getItem(`dish_reviews_${dishId}`) || '[]');
        setReviews(reviews);
        
        //   
        if (reviews.length > 0) {
          const avgRating = reviews.reduce((sum, review) => sum + review.rating, 0) / reviews.length;
          setDishRating(avgRating);
          setDishReviewsCount(reviews.length);
        }
      } else {
        console.error('  ');
        navigate('/chef/menu');
      }
    } catch (error) {
      console.error('   :', error);
    } finally {
      setLoading(false);
    }
  }, [dishId, chefId, navigate]);

  useEffect(() => {
    loadDishData();
  }, [dishId, chefId, loadDishData]);

  const formatDate = (dateString) => {
    if (!dateString || dateString === 'Invalid Date') {
      return ' ';
    }
    return new Date(dateString).toLocaleDateString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getSpecializationName = (specialization) => {
    const specializations = {
      'general': ' ',
      'tatar': ' ',
      'european': ' ',
      'asian': ' ',
      'vegetarian': ' ',
      'halal': ' '
    };
    return specializations[specialization] || specialization;
  };

  const getCategoryName = (categoryId) => {
    const categories = {
      'halal': ' ',
      'preparations': '',
      'bakery': '',
      'tatar': ' ',
      'soups': '',
      'salads': '',
      'desserts': '',
      'diet': '   ',
      'client_products': '    ',
      'master_class': '  ',
      'help_cooking': '     ',
      'mainDishes': ' '
    };
    return categories[categoryId] || categoryId || ' ';
  };

  const getDishHistory = () => {
    if (!dish) return [];
    
    const history = [];
    
    //  
    if (dish.createdAt) {
      history.push({
        date: dish.createdAt,
        action: ' ',
        description: '    '
      });
    } else {
      //    ,   
      history.push({
        date: new Date().toISOString(),
        action: ' ',
        description: '    '
      });
    }
    
    //  
    if (dish.updatedAt && dish.updatedAt !== dish.createdAt) {
      history.push({
        date: dish.updatedAt,
        action: ' ',
        description: '   '
      });
    }
    
    //  
    if (dish.orders && dish.orders > 0) {
      history.push({
        date: new Date().toISOString(),
        action: ' ',
        description: ` ${dish.orders} `
      });
    }
    
    return history.sort((a, b) => new Date(b.date) - new Date(a.date));
  };

  if (loading) {
    return (
      <div className="dish-passport">
        <div className="loading">  ...</div>
      </div>
    );
  }

  if (error || !dish) {
    return (
      <div className="dish-passport">
        <div className="error-message">
          <h2> </h2>
          <p>{error || '  '}</p>
          <button onClick={() => navigate(-1)} className="back-button">
             
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="dish-passport">
      <div className="passport-header">
        <button onClick={() => navigate(-1)} className="back-button">
           
        </button>
        <h1>  </h1>
        <div className="qr-code-section">
          <img src={qrCode} alt="QR- " className="qr-code" />
          <p className="qr-note"> QR-     </p>
        </div>
      </div>

      <div className="passport-content">
        {/*     */}
        <div className="dish-info-section">
          <div className="dish-image">
            {(dish.image || dish.photo) ? (
              <img src={dish.image || dish.photo} alt={dish.name} />
            ) : (
              <div className="no-image"></div>
            )}
          </div>
          <div className="dish-details">
            <h2>{dish.name}</h2>
            <p className="dish-description">{dish.description}</p>
            <div className="dish-meta">
              <span className="price">{dish.price} </span>
              <span className="category">{getCategoryName(dish.category_id || dish.category)}</span>
              <div className="dish-rating">
                <Rating 
                  rating={dishRating} 
                  readOnly={true} 
                  size="medium" 
                  showValue={true}
                  showCount={true}
                  count={dishReviewsCount}
                />
                {dishRating === 0 && (
                  <span className="no-rating">  </span>
                )}
              </div>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="chef-info-section">
          <h3> </h3>
          <div className="chef-details">
            <div className="chef-avatar">
              {chef?.avatar ? (
                <img src={chef.avatar} alt={chef.name} />
              ) : (
                <div className="avatar-placeholder">
                  {chef?.name ? chef.name.charAt(0).toUpperCase() : (chef?.email ? chef.email.charAt(0).toUpperCase() : '')}
                </div>
              )}
            </div>
            <div className="chef-info">
              <h4>{chef?.name || ' '}</h4>
              <p className="chef-email">{chef?.email || chefId}</p>
              <p className="chef-specialization">
                : {getSpecializationName(chef?.specialization) || ' '}
              </p>
              <p className="chef-experience">: {chef?.experience || '0'} </p>
              <div className="chef-rating">
                <span className="rating-label">:</span>
                <span className="rating-value">
                  {chefRating > 0 ? ` ${chefRating.toFixed(1)}/5` : '  '}
                </span>
                {chefReviewsCount > 0 && (
                  <span className="reviews-count">({chefReviewsCount} )</span>
                )}
              </div>
            </div>
          </div>
        </div>

        {/*     */}
        <div className="nutrition-section">
          <h3>    </h3>
          <div className="ingredients">
            <h4>:</h4>
            <p className="ingredients-text">{dish.ingredients || ' '}</p>
          </div>
          <div className="nutrition-grid">
            <div className="nutrition-item">
              <span className="nutrition-label">:</span>
              <span className="nutrition-value">{dish.calories || '/'} </span>
            </div>
            <div className="nutrition-item">
              <span className="nutrition-label">:</span>
              <span className="nutrition-value">{dish.protein || '/'} </span>
            </div>
            <div className="nutrition-item">
              <span className="nutrition-label">:</span>
              <span className="nutrition-value">{dish.carbs || '/'} </span>
            </div>
            <div className="nutrition-item">
              <span className="nutrition-label">:</span>
              <span className="nutrition-value">{dish.fat || '/'} </span>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="history-section">
          <h3>  </h3>
          <div className="history-timeline">
            {getDishHistory().map((event, index) => (
              <div key={index} className="history-item">
                <div className="history-date">
                  {formatDate(event.date)}
                </div>
                <div className="history-content">
                  <h4>{event.action}</h4>
                  <p>{event.description}</p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/*   */}
        <div className="additional-info-section">
          <h3>  </h3>
          <div className="info-grid">
            <div className="info-item">
              <span className="info-label"> :</span>
              <span className="info-value">{dish.createdAt ? formatDate(dish.createdAt) : ' '}</span>
            </div>
            <div className="info-item">
              <span className="info-label"> :</span>
              <span className="info-value">{dish.updatedAt ? formatDate(dish.updatedAt) : (dish.createdAt ? formatDate(dish.createdAt) : ' ')}</span>
            </div>
            <div className="info-item">
              <span className="info-label">:</span>
              <span className="info-value status-active"></span>
            </div>
            <div className="info-item">
              <span className="info-label">:</span>
              <span className="info-value status-verified"> </span>
            </div>
          </div>
        </div>

        {/*   */}
        {(dish.before_photo || dish.after_photo) && (
          <div className="photos-section">
            <h3>   </h3>
            <div className="photos-grid">
              {dish.before_photo && (
                <div className="photo-item">
                  <h4> </h4>
                  <img src={dish.before_photo} alt=" " className="process-photo" />
                </div>
              )}
              {dish.after_photo && (
                <div className="photo-item">
                  <h4> </h4>
                  <img src={dish.after_photo} alt=" " className="process-photo" />
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default DishPassport;
