import React, { useState, useEffect, useCallback } from 'react';
import { useToast } from '../contexts/ToastContext';
import '../App.css';

const ChefProcurementPlanner = ({ onClose }) => {
  const { showSuccess, showError } = useToast();
  const [orders, setOrders] = useState([]);
  const [recommendations, setRecommendations] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedPeriod, setSelectedPeriod] = useState('week'); // week, month, quarter
  const [addedToShoppingList, setAddedToShoppingList] = useState(new Set()); //   
  const [removedRecommendations, setRemovedRecommendations] = useState(new Set()); //   

  //  
  const getIngredientIcon = (ingredient) => {
    const normalizedIngredient = ingredient.toLowerCase().trim();
    
    //   
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    
    // 
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    
    // 
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    
    //  
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes(' ')) return '';
    
    //   
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    
    // 
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    
    // 
    if (normalizedIngredient.includes('')) return '';
    
    //   
    if (normalizedIngredient.includes('')) return '';
    if (normalizedIngredient.includes('')) return '';
    
    // 
    if (normalizedIngredient.includes('') || normalizedIngredient.includes('')) return '';
    
    // 
    if (normalizedIngredient.includes('')) return '';
    
    //  
    return '';
  };

  const getChefDishes = (chefId) => {
    try {
      const key = `demo_menu_${chefId}`;
      const raw = localStorage.getItem(key);
      let parsed = raw ? JSON.parse(raw) : [];
      
      //   ,     
      if (!Array.isArray(parsed) || parsed.length === 0) {
        console.log('     :', chefId);
        parsed = [
          {
            id: 'dish-1',
            name: '',
            category: '',
            price: 350,
            description: '  ',
            ingredients: `500 
300 
200 
150 
100 
50  
2  
,   `,
            image: '',
            chefId: chefId
          },
          {
            id: 'dish-2',
            name: '',
            category: ' ',
            price: 400,
            description: '   ',
            ingredients: `400 
300 
200 
100 
50  
2  
,   `,
            image: '',
            chefId: chefId
          },
          {
            id: 'dish-3',
            name: ' -',
            category: ' ',
            price: 450,
            description: '  -',
            ingredients: `300  
100  
50 
2 
100  
,   `,
            image: '',
            chefId: chefId
          },
          {
            id: 'dish-4',
            name: ' ',
            category: '',
            price: 280,
            description: '  ',
            ingredients: `200  
150  
50 
30 
2 
50  
,   `,
            image: '',
            chefId: chefId
          },
          {
            id: 'dish-5',
            name: '-',
            category: '',
            price: 250,
            description: '   ',
            ingredients: `300  
200 
150 
100 
50  
, ,   `,
            image: '',
            chefId: chefId
          }
        ];
        
        //     localStorage
        localStorage.setItem(key, JSON.stringify(parsed));
        console.log('     ');
      }
      
      return Array.isArray(parsed) ? parsed : [];
    } catch (error) {
      console.error('Error loading chef dishes:', error);
      return [];
    }
  };

  const getRealDishIngredients = (dishName, chefDishes) => {
    //      
    const dish = chefDishes.find(d => d.name === dishName);
    
    console.log('  :', dishName);
    console.log('   :', chefDishes.map(d => d.name));
    console.log('  :', dish);
    
    if (dish && dish.ingredients) {
      console.log('  :', dish.ingredients);
      //     
      const parsed = parseIngredientsText(dish.ingredients);
      console.log('  :', parsed);
      return parsed;
    }
    
    console.log('  fallback  :', dishName);
    //     ,  fallback
    return getDishIngredients(dishName);
  };

  const parseIngredientsText = (ingredientsText) => {
    if (!ingredientsText || typeof ingredientsText !== 'string') {
      return [];
    }
    
    const ingredients = [];
    const lines = ingredientsText.split('\n').filter(line => line.trim());
    
    lines.forEach(line => {
      const trimmedLine = line.trim();
      
      //    " " -     
      if (trimmedLine.includes(' ') || trimmedLine.includes('') || trimmedLine.includes('')) {
        return; //   
      }
      
      //    "500 "  "2  "
      const match = trimmedLine.match(/^(\d+(?:\.\d+)?)\s*(|||||\.|\.|||||)?\s*(.+)$/i);
      
      if (match) {
        const amount = parseFloat(match[1]);
        const unit = match[2] || '';
        const name = match[3].trim();
        
        //     
        let amountInGrams = amount;
        if (unit === '') amountInGrams = amount * 1000;
        else if (unit === '' || unit === '') amountInGrams = amount; //     
        else if (unit === '' || unit === '' || unit === '' || unit === '' || unit === '' || unit === '') {
          amountInGrams = amount * 50; //     
        }
        
        ingredients.push({
          name: name,
          amount: amountInGrams / 1000, //     
          unit: unit
        });
      } else {
        //    ,  (    )
        console.log('   :', trimmedLine);
      }
    });
    
    return ingredients;
  };

  const getDishIngredients = (dishName) => {
    //     (fallback)
    const ingredientsMap = {
      '  ': [
        { name: ' ', amount: 0.2 },
        { name: ' ', amount: 0.1 },
        { name: '', amount: 0.05 },
        { name: '', amount: 0.03 }
      ],
      '  ': [
        { name: '', amount: 0.3 },
        { name: '', amount: 0.2 },
        { name: '', amount: 0.1 },
        { name: '', amount: 0.1 }
      ],
      '  ""': [
        { name: '', amount: 0.15 },
        { name: ' ', amount: 0.1 },
        { name: '', amount: 0.08 },
        { name: ' ', amount: 0.1 }
      ]
    };
    return ingredientsMap[dishName] || [
      { name: ' ', amount: 0.5 }
    ];
  };

  const getIngredientPrice = (ingredient) => {
    //     
    const prices = {
      //   
      ' ': 300, '': 300, ' ': 300,
      '': 500, ' ': 500, ' ': 450,
      '': 400, ' ': 400, ' ': 350,
      '': 600, ' ': 600,
      '': 300, '': 800, '': 400, '': 200,
      
      // 
      '': 50, '': 50,
      '': 80, ' ': 80, ' ': 120,
      '': 60, '': 60,
      '': 150, '': 150, '': 150,
      '': 120, '': 120,
      '': 40, ' ': 40,
      '': 200, ' ': 200,
      '': 300, '': 300,
      
      // 
      '': 150, ' ': 150, ' ': 150,
      '': 100, '': 100,
      '': 100, '': 100,
      '': 200, '': 200,
      
      //  
      '': 80, '': 80,
      '': 400, '': 400, '': 800, '': 500,
      '': 200, '': 200,
      '': 150, '': 150,
      ' ': 200, '': 200,
      
      //   
      '': 40, '': 40, ' ': 40,
      '': 80, '': 80, ' ': 100,
      '': 100, '': 100,
      '': 60, '': 60,
      
      //   
      ' ': 600, ' ': 600,
      '': 800, '': 800,
      '': 300, '': 300,
      
      //   
      '': 20, '': 20,
      ' ': 100, '': 100,
      ' ': 50, '': 50,
      
      // 
      ' ': 100, ' ': 100,
      ' ': 300, ' ': 300,
      
      // 
      '': 80, '': 80, ' ': 80,
      
      //   
      '': 60, '': 60, ' ': 60,
      '': 300, '': 300,
      
      // 
      '': 200, ' ': 200,
      '': 150, ' ': 150
    };
    
    //     
    const normalizedIngredient = ingredient.toLowerCase().trim();
    
    //   
    for (const [key, price] of Object.entries(prices)) {
      if (key.toLowerCase() === normalizedIngredient) {
        return price;
      }
    }
    
    //   
    for (const [key, price] of Object.entries(prices)) {
      if (normalizedIngredient.includes(key.toLowerCase()) || key.toLowerCase().includes(normalizedIngredient)) {
        return price;
      }
    }
    
    //      
    return 150;
  };

  const generateRecommendations = (orders) => {
    console.log('    :', orders);
    
    //      
    const dishPortions = {};
    let totalOrders = 0;
    
    orders.forEach(order => {
      totalOrders++;
      order.items?.forEach(item => {
        console.log('  :', item.name, ':', item.quantity);
        
        //    
        dishPortions[item.name] = (dishPortions[item.name] || 0) + item.quantity;
      });
    });
    
    console.log('    :', dishPortions);
    console.log('  :', totalOrders);

    //      
    const recommendations = [];
    
    //      
    const chefId = localStorage.getItem('chefId') || 'demo-chef-1';
    const chefDishes = getChefDishes(chefId);
    
    //     
    const topDishes = Object.entries(dishPortions)
      .sort(([,a], [,b]) => b - a)
      .slice(0, 5); //  -5 
    
    topDishes.forEach(([dishName, totalPortions], index) => {
      //        
      const dish = chefDishes.find(d => d.name === dishName);
      const dishPrice = dish ? dish.price : 200; // Fallback 
      
      //     
      const recommendedPortions = Math.ceil(totalPortions * 1.2); // +20% 
      
      //  
      const estimatedCost = recommendedPortions * dishPrice;
      
      //     
      let priority = 'low';
      if (totalPortions >= 5) priority = 'high';
      else if (totalPortions >= 3) priority = 'medium';
      
      recommendations.push({
        id: `dish-${index}`,
        type: 'dish',
        name: dishName,
        priority: priority,
        reason: ` ${totalPortions}   ${totalOrders} `,
        suggestedAmount: recommendedPortions,
        unit: '',
        estimatedCost: estimatedCost,
        icon: '',
        //    
        actualPortions: totalPortions,
        dishPrice: dishPrice
      });
    });

    //  
    const seasonalRecommendations = getSeasonalRecommendations();
    recommendations.push(...seasonalRecommendations);

    console.log('  :', recommendations);
    setRecommendations(recommendations);
  };

  const getSeasonalRecommendations = () => {
    const month = new Date().getMonth();
    const seasonal = [];
    
    if (month >= 2 && month <= 4) { // 
      seasonal.push({
        id: 'seasonal-spring-1',
        type: 'seasonal',
        name: ' ',
        priority: 'medium',
        reason: '  -    ',
        suggestedAmount: 2,
        unit: '',
        estimatedCost: 300,
        icon: ''
      });
    }
    
    if (month >= 5 && month <= 7) { // 
      seasonal.push({
        id: 'seasonal-summer-1',
        type: 'seasonal',
        name: ' ',
        priority: 'high',
        reason: '  -   ',
        suggestedAmount: 5,
        unit: '',
        estimatedCost: 500,
        icon: ''
      });
    }
    
    return seasonal;
  };

  const loadOrders = useCallback(async () => {
    try {
      setLoading(true);
      const clientOrders = JSON.parse(localStorage.getItem('clientOrders') || '[]');
      const chefId = localStorage.getItem('chefId') || 'demo-chef-1';
      
      //      
      const now = new Date();
      const periodStart = new Date();
      
      switch (selectedPeriod) {
        case 'week':
          periodStart.setDate(now.getDate() - 7);
          break;
        case 'month':
          periodStart.setMonth(now.getMonth() - 1);
          break;
        case 'quarter':
          periodStart.setMonth(now.getMonth() - 3);
          break;
        default:
          periodStart.setDate(now.getDate() - 7);
      }
      
      const chefOrders = clientOrders.filter(order => {
        const orderDate = new Date(order.createdAt);
        return order.items?.some(item => item.chefId === chefId) && 
               orderDate >= periodStart;
      });
      
      setOrders(chefOrders);
      generateRecommendations(chefOrders);
    } catch (error) {
      console.error('Error loading orders:', error);
      showError('  ');
    } finally {
      setLoading(false);
    }
  }, [selectedPeriod, showError, generateRecommendations]);

  useEffect(() => {
    loadOrders();
  }, [loadOrders]);

  const getPriorityColor = (priority) => {
    switch (priority) {
      case 'high': return '#e74c3c';
      case 'medium': return '#f39c12';
      case 'low': return '#27ae60';
      default: return '#95a5a6';
    }
  };

  const getPriorityText = (priority) => {
    switch (priority) {
      case 'high': return '';
      case 'medium': return '';
      case 'low': return '';
      default: return '';
    }
  };

  const handleAddToShoppingList = (recommendation) => {
    try {
      const shoppingList = JSON.parse(localStorage.getItem('chefShoppingList') || '[]');
      
      //   ,   
      if (recommendation.type === 'dish') {
        const chefId = localStorage.getItem('chefId') || 'demo-chef-1';
        const chefDishes = getChefDishes(chefId);
        const ingredients = getRealDishIngredients(recommendation.name, chefDishes);
        
        console.log('     :', recommendation.name);
        console.log('   :', ingredients);
        
        //      
        ingredients.forEach(ingredient => {
          const existingItem = shoppingList.find(item => item.name === ingredient.name);
          
          if (existingItem) {
            //  
            existingItem.suggestedAmount += Math.ceil(ingredient.amount * recommendation.suggestedAmount);
            existingItem.estimatedCost = Math.ceil(existingItem.suggestedAmount * getIngredientPrice(ingredient.name));
          } else {
            //   
            shoppingList.push({
              id: `ingredient-${Date.now()}-${Math.random()}`,
              type: 'ingredient',
              name: ingredient.name,
              priority: recommendation.priority,
              reason: ` : ${recommendation.name}`,
              suggestedAmount: Math.ceil(ingredient.amount * recommendation.suggestedAmount),
              unit: ingredient.unit || '',
              estimatedCost: Math.ceil(ingredient.amount * recommendation.suggestedAmount * getIngredientPrice(ingredient.name)),
              icon: getIngredientIcon(ingredient.name),
              addedAt: new Date().toISOString(),
              purchased: false
            });
          }
        });
      } else {
        //    ,   
      const existingItem = shoppingList.find(item => item.name === recommendation.name);
      
      if (existingItem) {
          existingItem.suggestedAmount += recommendation.suggestedAmount;
        existingItem.estimatedCost += recommendation.estimatedCost;
      } else {
        shoppingList.push({
          ...recommendation,
          addedAt: new Date().toISOString(),
          purchased: false
        });
        }
      }
      
      localStorage.setItem('chefShoppingList', JSON.stringify(shoppingList));
      
      //        
      setAddedToShoppingList(prev => new Set([...prev, recommendation.id]));
      setRemovedRecommendations(prev => new Set([...prev, recommendation.id]));
      
      showSuccess(`${recommendation.name}        `);
    } catch (error) {
      console.error('Error adding to shopping list:', error);
      showError('    ');
    }
  };

  const handleRestoreRecommendation = (recommendationId) => {
    try {
      //     
      setRemovedRecommendations(prev => {
        const newSet = new Set(prev);
        newSet.delete(recommendationId);
        return newSet;
      });
      
      //      
      setAddedToShoppingList(prev => {
        const newSet = new Set(prev);
        newSet.delete(recommendationId);
        return newSet;
      });
      
      showSuccess(' ');
    } catch (error) {
      console.error('Error restoring recommendation:', error);
      showError('  ');
    }
  };

  if (loading) {
    return (
      <div className="chef-procurement-modal-overlay">
        <div className="chef-procurement-modal">
          <div style={{ textAlign: 'center', padding: '40px' }}>
            <div className="loading-spinner"></div>
            <p> ...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="chef-procurement-modal-overlay" onClick={onClose}>
      <div className="chef-procurement-modal" onClick={(e) => e.stopPropagation()}>
        <div className="chef-procurement-header">
          <h3> AI  </h3>
          <button onClick={onClose} className="back-button"></button>
        </div>

        <div className="chef-procurement-content">
          {/*   */}
          <div className="period-selector">
            <h4>  </h4>
            <div className="period-buttons">
              <button 
                className={selectedPeriod === 'week' ? 'active' : ''}
                onClick={() => setSelectedPeriod('week')}
              >
                
              </button>
              <button 
                className={selectedPeriod === 'month' ? 'active' : ''}
                onClick={() => setSelectedPeriod('month')}
              >
                
              </button>
              <button 
                className={selectedPeriod === 'quarter' ? 'active' : ''}
                onClick={() => setSelectedPeriod('quarter')}
              >
                
              </button>
            </div>
          </div>

          {/*  */}
          <div className="procurement-stats">
            <h4>  </h4>
            <div className="stats-grid">
              <div className="stat-item">
                <div className="stat-number">{orders.length}</div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-number">
                  {orders.reduce((sum, order) => sum + (order.items?.length || 0), 0)}
                </div>
                <div className="stat-label"></div>
              </div>
              <div className="stat-item">
                <div className="stat-number">
                  {orders.reduce((sum, order) => sum + (order.total || 0), 0)}
                </div>
                <div className="stat-label"></div>
              </div>
            </div>
          </div>

          {/*   */}
          {removedRecommendations.size > 0 && (
            <div className="recommendations-section">
              <h4>  </h4>
              <div className="recommendations-list">
                {recommendations
                  .filter(recommendation => removedRecommendations.has(recommendation.id))
                  .map(recommendation => (
                    <div key={recommendation.id} className="recommendation-card" style={{ opacity: 0.7, border: '2px dashed #ccc' }}>
                      <div className="recommendation-header">
                        <div className="recommendation-icon">{recommendation.icon}</div>
                        <div className="recommendation-info">
                          <h5>{recommendation.name}</h5>
                          <p className="recommendation-reason">{recommendation.reason}</p>
                        </div>
                        <div className="recommendation-priority">
                          <span 
                            className="priority-badge"
                            style={{ backgroundColor: getPriorityColor(recommendation.priority) }}
                          >
                            {getPriorityText(recommendation.priority)}
                          </span>
                        </div>
                      </div>
                      
                      <div className="recommendation-details">
                        <div className="detail-item">
                          <strong> :</strong> {recommendation.actualPortions || 0}
                        </div>
                        <div className="detail-item">
                          <strong>:</strong> {recommendation.suggestedAmount} {recommendation.unit} (+20% )
                        </div>
                        <div className="detail-item">
                          <strong>  :</strong> {recommendation.dishPrice || 200}
                        </div>
                        <div className="detail-item">
                          <strong> :</strong> {recommendation.estimatedCost}
                        </div>
                      </div>
                      
                      <div className="recommendation-actions">
                        <button 
                          className="add-to-list-btn"
                          onClick={() => handleRestoreRecommendation(recommendation.id)}
                          style={{ background: '#28a745', color: 'white' }}
                        >
                           
                        </button>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          )}

          {/*  */}
          <div className="recommendations-section">
            <h4> AI </h4>
            <div className="recommendations-list">
              {recommendations
                .filter(recommendation => !removedRecommendations.has(recommendation.id))
                .map(recommendation => (
                <div key={recommendation.id} className="recommendation-card">
                  <div className="recommendation-header">
                    <div className="recommendation-icon">{recommendation.icon}</div>
                    <div className="recommendation-info">
                      <h5>{recommendation.name}</h5>
                      <p className="recommendation-reason">{recommendation.reason}</p>
                    </div>
                    <div className="recommendation-priority">
                      <span 
                        className="priority-badge"
                        style={{ backgroundColor: getPriorityColor(recommendation.priority) }}
                      >
                        {getPriorityText(recommendation.priority)}
                      </span>
                    </div>
                  </div>
                  
                  <div className="recommendation-details">
                    <div className="detail-item">
                      <strong> :</strong> {recommendation.actualPortions || 0}
                    </div>
                    <div className="detail-item">
                      <strong>:</strong> {recommendation.suggestedAmount} {recommendation.unit} (+20% )
                    </div>
                    <div className="detail-item">
                      <strong>  :</strong> {recommendation.dishPrice || 200}
                    </div>
                    <div className="detail-item">
                      <strong> :</strong> {recommendation.estimatedCost}
                    </div>
                  </div>
                  
                  <div className="recommendation-actions">
                    <button 
                      className={`add-to-list-btn ${addedToShoppingList.has(recommendation.id) ? 'added' : ''}`}
                      onClick={() => handleAddToShoppingList(recommendation)}
                      disabled={addedToShoppingList.has(recommendation.id)}
                    >
                      {addedToShoppingList.has(recommendation.id) ? ' ' : '    '}
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChefProcurementPlanner;
