import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const AdminChefs = () => {
  const [chefs, setChefs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedChef, setSelectedChef] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    //  
    const token = localStorage.getItem('authToken');
    const role = localStorage.getItem('role');
    
    if (!token || role !== 'admin') {
      navigate('/admin/login');
      return;
    }

    loadChefs();
  }, [navigate]);

  const loadChefs = () => {
    setLoading(true);
    
    //      chef
    const allUsers = JSON.parse(localStorage.getItem('allUsers') || '[]');
    const chefUsers = allUsers.filter(user => user.role === 'chef');
    
    //       
    const orders = JSON.parse(localStorage.getItem('clientOrders') || '[]');
    
    const chefsWithStats = chefUsers.map(chef => {
      const chefOrders = orders.filter(order => order.chefId === chef.email);
      const totalRevenue = chefOrders.reduce((sum, order) => sum + (order.payment?.total || 0), 0);
      const completedOrders = chefOrders.filter(order => order.status === 'delivered').length;
      
      return {
        ...chef,
        totalOrders: chefOrders.length,
        totalRevenue,
        completedOrders,
        rating: calculateChefRating(chef.email)
      };
    });
    
    setChefs(chefsWithStats);
    setLoading(false);
  };

  const calculateChefRating = (chefEmail) => {
    const reviews = JSON.parse(localStorage.getItem('chefReviews') || '[]');
    const chefReviews = reviews.filter(review => review.chefId === chefEmail);
    
    if (chefReviews.length === 0) return 0;
    
    const totalRating = chefReviews.reduce((sum, review) => sum + review.rating, 0);
    return (totalRating / chefReviews.length).toFixed(1);
  };

  const handleChefClick = (chef) => {
    setSelectedChef(chef);
    setShowModal(true);
  };

  const handleBlockChef = (chefEmail) => {
    const allUsers = JSON.parse(localStorage.getItem('allUsers') || '[]');
    const updatedUsers = allUsers.map(user => 
      user.email === chefEmail 
        ? { ...user, blocked: !user.blocked }
        : user
    );
    localStorage.setItem('allUsers', JSON.stringify(updatedUsers));
    loadChefs();
  };

  const allDocumentsUploaded = (chef) => {
    return chef.documents?.passport && 
           chef.documents?.healthBook && 
           chef.documents?.selfEmployment && 
           chef.documents?.kitchenCertificate &&
           chef.documents?.kitchenPhotos?.length > 0 &&
           chef.documents?.dishPhotos?.length >= 4;
  };

  const handleVerifyDocuments = (chefEmail, approved) => {
    const allUsers = JSON.parse(localStorage.getItem('allUsers') || '[]');
    const updatedUsers = allUsers.map(user => 
      user.email === chefEmail 
        ? { 
            ...user, 
            documentsVerified: approved,
            documentsVerifiedAt: new Date().toISOString(),
            documentsVerifiedBy: 'admin',
            verified: approved
          }
        : user
    );
    localStorage.setItem('allUsers', JSON.stringify(updatedUsers));
    loadChefs();
    
    if (approved) {
      alert('  !    .');
    } else {
      alert('  !  .');
    }
  };

  const handleRequestDocuments = (chefEmail) => {
    const chef = chefs.find(c => c.email === chefEmail);
    if (!chef) return;
    
    const missingDocs = [];
    if (!chef.documents?.passport) missingDocs.push('/ ');
    if (!chef.documents?.healthBook) missingDocs.push('');
    if (!chef.documents?.selfEmployment) missingDocs.push('  /');
    if (!chef.documents?.kitchenCertificate) missingDocs.push('- ');
    if (!chef.documents?.kitchenPhotos?.length) missingDocs.push(' ');
    if (!chef.documents?.dishPhotos?.length || chef.documents.dishPhotos.length < 4) missingDocs.push('  ( 4)');
    
    if (missingDocs.length === 0) {
      alert('  !');
      return;
    }
    
    const message = ` ${chef.name || chefEmail}       :\n\n${missingDocs.join('\n')}\n\n         6-12 .`;
    alert(message);
    
    //        
    console.log('  :', chefEmail, ' :', missingDocs);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
  };

  if (loading) {
    return (
      <div className="admin-chefs">
        <div className="loading"> ...</div>
      </div>
    );
  }

  return (
    <div className="admin-chefs">
      <div className="page-header">
        <h1>  </h1>
        <div className="header-actions">
          <button onClick={loadChefs} className="refresh-button">
             
          </button>
        </div>
      </div>

      {chefs.length === 0 ? (
        <div className="no-data">
          <p>  </p>
        </div>
      ) : (
        <div className="chefs-grid">
          {chefs.map(chef => (
            <div key={chef.email} className="chef-card">
              <div className="chef-avatar">
                {chef.avatar ? (
                  <img src={chef.avatar} alt={chef.name} />
                ) : (
                  <div className="avatar-placeholder">
                    {chef.name ? chef.name.charAt(0).toUpperCase() : ''}
                  </div>
                )}
              </div>
              
              <div className="chef-info">
                <h3>{chef.name || ' '}</h3>
                <p className="chef-email">{chef.email}</p>
                <p className="chef-specialization">{chef.specialization || ' '}</p>
                
                <div className="chef-stats">
                  <div className="stat">
                    <span className="stat-label">:</span>
                    <span className="stat-value">
                      {chef.rating > 0 ? ` ${chef.rating}/5` : ' '}
                    </span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">:</span>
                    <span className="stat-value">{chef.totalOrders}</span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">:</span>
                    <span className="stat-value">{chef.totalRevenue.toLocaleString()} </span>
                  </div>
                </div>
                
                <div className="chef-actions">
                  <button 
                    onClick={() => handleChefClick(chef)}
                    className="view-button"
                  >
                     
                  </button>
                  <button 
                    onClick={() => handleBlockChef(chef.email)}
                    className={chef.blocked ? 'unblock-button' : 'block-button'}
                  >
                    {chef.blocked ? ' ' : ' '}
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      {/*        */}
      {showModal && selectedChef && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h2>  </h2>
              <button 
                onClick={() => setShowModal(false)}
                className="close-button"
              >
                
              </button>
            </div>
            
            <div className="modal-body">
              <div className="chef-details">
                <div className="chef-avatar-large">
                  {selectedChef.avatar ? (
                    <img src={selectedChef.avatar} alt={selectedChef.name} />
                  ) : (
                    <div className="avatar-placeholder-large">
                      {selectedChef.name ? selectedChef.name.charAt(0).toUpperCase() : ''}
                    </div>
                  )}
                </div>
                
                <div className="chef-details-info">
                  <h3>{selectedChef.name || ' '}</h3>
                  <p><strong>Email:</strong> {selectedChef.email}</p>
                  <p><strong>:</strong> {selectedChef.specialization || ' '}</p>
                  <p><strong>:</strong> {selectedChef.experience || ' '} </p>
                  <p><strong>:</strong> {selectedChef.description || ' '}</p>
                  <p><strong> :</strong> {formatDate(selectedChef.createdAt)}</p>
                  <p><strong>:</strong> 
                    <span className={selectedChef.blocked ? 'status-blocked' : 'status-active'}>
                      {selectedChef.blocked ? '' : ''}
                    </span>
                  </p>
                </div>
              </div>
              
              {/*   */}
              <div className="document-verification">
                <h4>   </h4>
                <div className="verification-section">
                  
                  {/*  */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5> / </h5>
                      <p>: 
                        <span className={`verification-status ${selectedChef.documents?.passport ? 'verified' : 'pending'}`}>
                          {selectedChef.documents?.passport ? ' ' : '  '}
                        </span>
                      </p>
                      {selectedChef.documents?.passport && (
                        <div className="document-actions">
                          <button 
                            onClick={() => window.open(selectedChef.documents.passport, '_blank')}
                            className="view-document-button"
                          >
                              
                          </button>
                        </div>
                      )}
                    </div>
                  </div>

                  {/*  */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5> </h5>
                      <p>: 
                        <span className={`verification-status ${selectedChef.documents?.healthBook ? 'verified' : 'pending'}`}>
                          {selectedChef.documents?.healthBook ? ' ' : '  '}
                        </span>
                      </p>
                      <p className="document-note">    6-12 </p>
                      {selectedChef.documents?.healthBook && (
                        <div className="document-actions">
                          <button 
                            onClick={() => window.open(selectedChef.documents.healthBook, '_blank')}
                            className="view-document-button"
                          >
                              
                          </button>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* / */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5>   /</h5>
                      <p>: 
                        <span className={`verification-status ${selectedChef.documents?.selfEmployment ? 'verified' : 'pending'}`}>
                          {selectedChef.documents?.selfEmployment ? ' ' : '  '}
                        </span>
                      </p>
                      <p className="document-note">    6-12 </p>
                      {selectedChef.documents?.selfEmployment && (
                        <div className="document-actions">
                          <button 
                            onClick={() => window.open(selectedChef.documents.selfEmployment, '_blank')}
                            className="view-document-button"
                          >
                              
                          </button>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* - */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5> - </h5>
                      <p>: 
                        <span className={`verification-status ${selectedChef.documents?.kitchenCertificate ? 'verified' : 'pending'}`}>
                          {selectedChef.documents?.kitchenCertificate ? ' ' : '  '}
                        </span>
                      </p>
                      {selectedChef.documents?.kitchenCertificate && (
                        <div className="document-actions">
                          <button 
                            onClick={() => window.open(selectedChef.documents.kitchenCertificate, '_blank')}
                            className="view-document-button"
                          >
                              
                          </button>
                        </div>
                      )}
                    </div>
                  </div>

                  {/*   */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5>  </h5>
                      <p>: 
                        <span className={`verification-status ${selectedChef.documents?.kitchenPhotos?.length > 0 ? 'verified' : 'pending'}`}>
                          {selectedChef.documents?.kitchenPhotos?.length > 0 ? `  ${selectedChef.documents.kitchenPhotos.length} ` : '  '}
                        </span>
                      </p>
                      {selectedChef.documents?.kitchenPhotos?.length > 0 && (
                        <div className="photo-gallery">
                          {selectedChef.documents.kitchenPhotos.map((photo, index) => (
                            <img 
                              key={index}
                              src={photo} 
                              alt={` ${index + 1}`}
                              className="document-photo"
                              onClick={() => window.open(photo, '_blank')}
                            />
                          ))}
                        </div>
                      )}
                    </div>
                  </div>

                  {/*   */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5>  </h5>
                      <p>: 
                        <span className={`verification-status ${selectedChef.documents?.dishPhotos?.length >= 4 ? 'verified' : 'pending'}`}>
                          {selectedChef.documents?.dishPhotos?.length > 0 ? `  ${selectedChef.documents.dishPhotos.length} ` : '  '}
                        </span>
                      </p>
                      {selectedChef.documents?.dishPhotos?.length > 0 && (
                        <div className="photo-gallery">
                          {selectedChef.documents.dishPhotos.map((photo, index) => (
                            <img 
                              key={index}
                              src={photo} 
                              alt={` ${index + 1}`}
                              className="document-photo"
                              onClick={() => window.open(photo, '_blank')}
                            />
                          ))}
                        </div>
                      )}
                    </div>
                  </div>

                  {/*   */}
                  <div className="document-item">
                    <div className="document-info">
                      <h5>  </h5>
                      <div className="info-section">
                        <p><strong> :</strong></p>
                        <p className="info-text">{selectedChef.documents?.experience || ' '}</p>
                      </div>
                      <div className="info-section">
                        <p><strong> :</strong></p>
                        <p className="info-text">{selectedChef.documents?.kitchenDescription || ' '}</p>
                      </div>
                      <div className="info-section">
                        <p><strong>  :</strong></p>
                        <p className="info-text">{selectedChef.documents?.uploadedAt ? new Date(selectedChef.documents.uploadedAt).toLocaleDateString('ru-RU') : ' '}</p>
                      </div>
                    </div>
                  </div>

                  <div className="verification-actions">
                    <button 
                      onClick={() => handleVerifyDocuments(selectedChef.email, true)}
                      className="verify-button"
                      disabled={!allDocumentsUploaded(selectedChef)}
                    >
                         
                    </button>
                    <button 
                      onClick={() => handleVerifyDocuments(selectedChef.email, false)}
                      className="reject-button"
                    >
                        
                    </button>
                    <button 
                      onClick={() => handleRequestDocuments(selectedChef.email)}
                      className="request-button"
                    >
                         
                    </button>
                  </div>
                </div>
              </div>

              <div className="chef-stats-detailed">
                <h4></h4>
                <div className="stats-grid">
                  <div className="stat-item">
                    <span className="stat-label"> </span>
                    <span className="stat-value">
                      {selectedChef.rating > 0 ? ` ${selectedChef.rating}/5` : ' '}
                    </span>
                  </div>
                  <div className="stat-item">
                    <span className="stat-label"> </span>
                    <span className="stat-value">{selectedChef.totalOrders}</span>
                  </div>
                  <div className="stat-item">
                    <span className="stat-label"> </span>
                    <span className="stat-value">{selectedChef.completedOrders}</span>
                  </div>
                  <div className="stat-item">
                    <span className="stat-label"> </span>
                    <span className="stat-value">{selectedChef.totalRevenue.toLocaleString()} </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminChefs;
