/**
 *        
 * @param {number} lat1 -   
 * @param {number} lon1 -   
 * @param {number} lat2 -   
 * @param {number} lon2 -   
 * @returns {number}   
 */
export const calculateDistance = (lat1, lon1, lat2, lon2) => {
  const R = 6371; //    
  const dLat = (lat2 - lat1) * Math.PI / 180;
  const dLon = (lon2 - lon1) * Math.PI / 180;
  const a = 
    Math.sin(dLat/2) * Math.sin(dLat/2) +
    Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * 
    Math.sin(dLon/2) * Math.sin(dLon/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
  const distance = R * c;
  return distance;
};

/**
 * ,        
 * @param {string} chefAddress -   
 * @param {string} clientAddress -  
 * @param {number} maxDistance -      (  5)
 * @returns {Promise<{inZone: boolean, distance: number, message: string}>}
 */
export const checkDeliveryZone = async (chefAddress, clientAddress, maxDistance = 5) => {
  try {
    //        API 
    //     
    const chefCoords = await geocodeAddress(chefAddress);
    const clientCoords = await geocodeAddress(clientAddress);
    
    const distance = calculateDistance(
      chefCoords.lat, 
      chefCoords.lon, 
      clientCoords.lat, 
      clientCoords.lon
    );
    
    const inZone = distance <= maxDistance;
    
    return {
      inZone,
      distance: Math.round(distance * 10) / 10,
      message: inZone 
        ? `  (${Math.round(distance * 10) / 10} )`
        : `  (${Math.round(distance * 10) / 10} ,  ${maxDistance} )`
    };
  } catch (error) {
    console.error('    :', error);
    return {
      inZone: false,
      distance: 0,
      message: '   '
    };
  }
};

/**
 *    (-)
 *        Google Maps API  Yandex Maps API
 * @param {string} address -   
 * @returns {Promise<{lat: number, lon: number}>}
 */
const geocodeAddress = async (address) => {
  // -  
  const demoCoords = {
    '': { lat: 55.8304, lon: 49.0661 },
    '': { lat: 55.7961, lon: 49.1064 },
    '-': { lat: 55.8500, lon: 49.1000 },
    '': { lat: 55.8000, lon: 49.2000 },
    '': { lat: 55.7900, lon: 49.1200 },
    '': { lat: 55.8200, lon: 49.0800 },
    '': { lat: 55.8400, lon: 49.1400 },
    '': { lat: 55.7800, lon: 49.1600 }
  };
  
  //    -
  const addressLower = address.toLowerCase();
  for (const [key, coords] of Object.entries(demoCoords)) {
    if (addressLower.includes(key)) {
      return coords;
    }
  }
  
  //   ,      
  return {
    lat: 55.8304 + (Math.random() - 0.5) * 0.1,
    lon: 49.0661 + (Math.random() - 0.5) * 0.1
  };
};

/**
 *        
 * @param {string} clientAddress -  
 * @param {Array} chefs -    
 * @param {number} maxDistance -    
 * @returns {Promise<Array>}     
 */
export const getChefsInDeliveryZone = async (clientAddress, chefs, maxDistance = 5) => {
  const chefsInZone = [];
  
  for (const chef of chefs) {
    if (chef.kitchenAddress) {
      const zoneCheck = await checkDeliveryZone(chef.kitchenAddress, clientAddress, maxDistance);
      if (zoneCheck.inZone) {
        chefsInZone.push({
          ...chef,
          distance: zoneCheck.distance,
          deliveryMessage: zoneCheck.message
        });
      }
    }
  }
  
  //    ( )
  return chefsInZone.sort((a, b) => a.distance - b.distance);
};

/**
 *   
 * @param {string} address -   
 * @returns {Object}  
 */
export const validateKitchenAddress = (address) => {
  if (!address || address.trim().length < 10) {
    return {
      isValid: false,
      message: '    10 '
    };
  }
  
  //     
  const addressKeywords = ['', '.', '', '.', '', '.', '', '.', '', '.'];
  const hasKeywords = addressKeywords.some(keyword => 
    address.toLowerCase().includes(keyword)
  );
  
  if (!hasKeywords) {
    return {
      isValid: false,
      message: '    (, , )'
    };
  }
  
  return {
    isValid: true,
    message: ' '
  };
};
