import React, { useState, useEffect, useCallback } from 'react';
import { useToast } from '../contexts/ToastContext';
import { updateOrderStatus } from '../api';
import '../App.css';

function ChefOrderDetails({ orderId, onClose, onStatusUpdate }) {
  const { showSuccess, showError } = useToast();
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState(false);

  //      
  const markNotificationAsRead = useCallback((orderId) => {
    try {
      const notifications = JSON.parse(localStorage.getItem('chefNotifications') || '[]');
      const updatedNotifications = notifications.map(notification => {
        if (notification.orderId === orderId && !notification.read) {
          return { ...notification, read: true };
        }
        return notification;
      });
      localStorage.setItem('chefNotifications', JSON.stringify(updatedNotifications));
      
      //     
      window.dispatchEvent(new CustomEvent('chefNotificationsUpdated'));
    } catch (error) {
      console.error('Error marking notification as read:', error);
    }
  }, []);

  const loadOrderDetails = useCallback(async () => {
    try {
      setLoading(true);
      
      //    localStorage
      const clientOrders = JSON.parse(localStorage.getItem('clientOrders') || '[]');
      const foundOrder = clientOrders.find(o => o.id === orderId);
      
      if (foundOrder) {
        console.log(' :', foundOrder);
        console.log(' :', {
          itemsTotal: foundOrder.itemsTotal,
          subtotal: foundOrder.subtotal,
          deliveryFee: foundOrder.deliveryFee,
          deliveryCost: foundOrder.deliveryCost,
          total: foundOrder.total,
          transaction: foundOrder.transaction,
          payment: foundOrder.payment
        });
        console.log(':', foundOrder.customer);
        console.log(':', foundOrder.delivery);
        console.log(':', foundOrder.comment);
        console.log(':', foundOrder.promo);
        console.log(':', foundOrder.discount);
        console.log(':', foundOrder.status);
        console.log(' :', foundOrder.createdAt);
        setOrder(foundOrder);
        
        //       
        markNotificationAsRead(orderId);
      } else {
        console.error('  :', orderId);
        console.log(' :', clientOrders.map(o => o.id));
        showError(` ${orderId}  `);
        onClose();
      }
    } catch (error) {
      console.error('Error loading order details:', error);
      showError('  ');
      onClose();
    } finally {
      setLoading(false);
    }
  }, [orderId, showError, markNotificationAsRead, onClose]);

  useEffect(() => {
    loadOrderDetails();
  }, [loadOrderDetails]);

  const handleStatusUpdate = async (newStatus) => {
    try {
      setUpdating(true);
      
      //    api.js   
      await updateOrderStatus(orderId, newStatus, order?.items?.[0]?.chefId || 'unknown');
      
      //   
      setOrder(prev => ({ ...prev, status: newStatus }));
      
      showSuccess(`   : ${getStatusText(newStatus)}`);
      
      if (onStatusUpdate) {
        onStatusUpdate(orderId, newStatus);
      }
    } catch (error) {
      console.error('Error updating order status:', error);
      showError('  ');
    } finally {
      setUpdating(false);
    }
  };


  const getStatusText = (status) => {
    const statusMap = {
      'pending': ' ',
      'pending_confirmation': ' ',
      'pending_payment': ' ',
      'confirmed': '',
      'preparing': '',
      'ready': '  ',
      'delivering': '',
      'delivered': '',
      'cancelled': ''
    };
    return statusMap[status] || status;
  };

  const getStatusColor = (status) => {
    const colorMap = {
      'pending': '#ff9800',
      'pending_confirmation': '#ff9800',
      'pending_payment': '#ff5722',
      'confirmed': '#2196f3',
      'preparing': '#ff5722',
      'ready': '#4caf50',
      'delivering': '#9c27b0',
      'delivered': '#8bc34a',
      'cancelled': '#f44336'
    };
    return colorMap[status] || '#666';
  };

  const formatTime = (timeString) => {
    try {
      const date = new Date(timeString);
      return date.toLocaleString('ru-RU', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return ' ';
    }
  };

  const formatDeliveryDate = (dateString) => {
    if (!dateString) return ' ';
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('ru-RU', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        weekday: 'long'
      });
    } catch {
      return dateString;
    }
  };

  const getDeliveryUrgency = (deliveryDate, deliveryTime) => {
    if (!deliveryDate || !deliveryTime) return null;
    
    try {
      const now = new Date();
      const delivery = new Date(`${deliveryDate} ${deliveryTime}`);
      const diffHours = (delivery - now) / (1000 * 60 * 60);
      
      if (diffHours < 1) {
        return <span className="urgency-urgent"> !    </span>;
      } else if (diffHours < 3) {
        return <span className="urgency-high"> !  3   </span>;
      } else if (diffHours < 24) {
        return <span className="urgency-medium">   {deliveryTime}</span>;
      } else {
        return <span className="urgency-low"> {formatDeliveryDate(deliveryDate)}  {deliveryTime}</span>;
      }
    } catch {
      return <span className="urgency-unknown">    </span>;
    }
  };

  // AI-  
  const getAITimerHint = (deliveryDate, deliveryTime) => {
    if (!deliveryDate || !deliveryTime) return null;
    
    try {
      const now = new Date();
      const delivery = new Date(`${deliveryDate} ${deliveryTime}`);
      const diffMinutes = Math.round((delivery - now) / (1000 * 60));
      
      if (diffMinutes > 0 && diffMinutes < 60) {
        return (
          <div className="ai-timer-hint">
            <div className="ai-icon"></div>
            <div className="ai-message">
              <strong>AI-:</strong>    {diffMinutes} .   #{order.id}
            </div>
          </div>
        );
      }
    } catch {
      return null;
    }
    return null;
  };

  const getPaymentMethodText = (method) => {
    const methods = {
      'card': '  ',
      'cash': '   ',
      'online': '  ',
      'apple_pay': ' Apple Pay',
      'google_pay': ' Google Pay'
    };
    return methods[method] || method || ' ';
  };

  const getPaymentStatus = (status) => {
    const statuses = {
      'pending': '  ',
      'paid': ' ',
      'failed': '  ',
      'refunded': ' '
    };
    return statuses[status] || status || ' ';
  };

  const getDeliveryReadinessStatus = (orderStatus) => {
    const statuses = {
      'pending': '  ',
      'confirmed': ' ',
      'preparing': ' ',
      'ready': '   ',
      'delivering': '  ',
      'delivered': ' ',
      'cancelled': ' '
    };
    return statuses[orderStatus] || '';
  };

  if (loading) {
    return (
      <div className="chef-order-details-modal-overlay">
        <div className="chef-order-details-modal">
          <div style={{ textAlign: 'center', padding: '40px' }}>
            <div className="loading-spinner"></div>
            <p>  ...</p>
            <p style={{ fontSize: '14px', color: '#666', marginTop: '10px' }}>
              ID : {orderId}
            </p>
          </div>
        </div>
      </div>
    );
  }

  if (!order) {
    return (
      <div className="chef-order-details-modal-overlay">
        <div className="chef-order-details-modal">
          <div style={{ textAlign: 'center', padding: '40px' }}>
            <p>  </p>
            <p style={{ fontSize: '14px', color: '#666', marginTop: '10px' }}>
              ID : {orderId}
            </p>
            <p style={{ fontSize: '12px', color: '#999', marginTop: '5px' }}>
                  
            </p>
            <button onClick={onClose} className="back-button">
              
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="chef-order-details-modal-overlay" onClick={onClose}>
      <div className="chef-order-details-modal" onClick={(e) => e.stopPropagation()}>
        <div className="chef-order-details-header">
          <h3>  #{order.id}</h3>
          <button onClick={onClose} className="back-button">
            
          </button>
        </div>

        <div className="chef-order-details-content">
          {/*    */}
          <div className="order-section">
            <h4>   </h4>
            <div className="order-info-grid">
              <div className="info-item">
                <strong>:</strong> {order.customer?.name || order.clientName || ' '}
              </div>
              <div className="info-item">
                <strong>:</strong> {order.customer?.phone || order.clientPhone || ' '}
              </div>
              <div className="info-item">
                <strong> :</strong> {order.delivery?.address || order.deliveryAddress || (order.delivery?.type === 'pickup' ? '' : ' ')}
              </div>
              <div className="info-item">
                <strong> :</strong> {formatTime(order.createdAt)}
              </div>
            </div>
          </div>

          {/*   */}
          <div className="order-section">
            <h4>  </h4>
            <div className="delivery-time-info">
              <div className="delivery-time-main">
                <div className="delivery-date">
                  <strong> :</strong> {formatDeliveryDate(order.delivery?.date)}
                </div>
                <div className="delivery-time">
                  <strong> :</strong> {order.delivery?.time || ' '}
                </div>
                <div className="delivery-type">
                  <strong> :</strong> {order.delivery?.type === 'pickup' ? '' : ''}
                </div>
              </div>
              <div className="delivery-urgency">
                {getDeliveryUrgency(order.delivery?.date, order.delivery?.time)}
              </div>
              {/* AI-   */}
              {getAITimerHint(order.delivery?.date, order.delivery?.time)}
            </div>
          </div>

          {/*   */}
          <div className="order-section">
            <h4>  </h4>
            <div className="payment-info">
              <div className="payment-method">
                <strong>:</strong> {getPaymentMethodText(order.payment?.method)}
              </div>
              <div className="payment-status">
                <strong>:</strong> {getPaymentStatus(order.payment?.status)}
              </div>
            </div>
          </div>

          {/*    */}
          {(order.comment || order.specialInstructions) && (
            <div className="order-section">
              <h4>   </h4>
              <div className="comments-info">
                {order.comment && (
                  <div className="comment-item">
                    <strong> :</strong>
                    <p>{order.comment}</p>
                  </div>
                )}
                {order.specialInstructions && (
                  <div className="comment-item">
                    <strong> :</strong>
                    <p>{order.specialInstructions}</p>
                  </div>
                )}
              </div>
            </div>
          )}

          {/*    ( ) */}
          {order.comment && (
            <div className="order-section">
              <h4>   </h4>
              <div className="order-comment">
                <p>{order.comment}</p>
              </div>
            </div>
          )}

          {/*    */}
          <div className="order-section">
            <h4>   </h4>
            <div className="delivery-info">
              <div className="delivery-code">
                <strong> :</strong> #{order.id}
              </div>
              <div className="delivery-address">
                <strong>:</strong> {order.delivery?.address || order.deliveryAddress || (order.delivery?.type === 'pickup' ? '' : ' ')}
              </div>
              <div className="delivery-contact">
                <strong>:</strong> {order.customer?.name || order.clientName || ' '} - {order.customer?.phone || order.clientPhone || ' '}
              </div>
              <div className="delivery-status">
                <strong> :</strong> {getDeliveryReadinessStatus(order.status)}
              </div>
            </div>
          </div>

          {/*   */}
          <div className="order-section">
            <h4>  </h4>
            <div 
              className="order-status"
              style={{ 
                backgroundColor: getStatusColor(order.status),
                color: 'white',
                padding: '8px 16px',
                borderRadius: '20px',
                display: 'inline-block',
                fontWeight: 'bold'
              }}
            >
              {getStatusText(order.status)}
            </div>
          </div>

          {/*    */}
          <div className="order-section">
            <h4>   </h4>
            <div className="order-items">
              {order.items && order.items.length > 0 ? (
                order.items.map((item, index) => (
                  <div key={index} className="order-item">
                    <div className="item-info">
                      <div className="item-name">{item.name}</div>
                      <div className="item-description">{item.description}</div>
                    </div>
                    <div className="item-details">
                      <div className="item-quantity">: {item.quantity}</div>
                      <div className="item-price">{item.price}  .</div>
                      <div className="item-total">: {item.price * item.quantity}</div>
                    </div>
                  </div>
                ))
              ) : (
                <p>   </p>
              )}
            </div>
          </div>

          {/*   */}
          <div className="order-section">
            <h4>  </h4>
            <div className="order-total">
              <div className="total-line">
                <span> :</span>
                <span>{order.itemsTotal || order.subtotal || (order.items ? order.items.reduce((sum, item) => sum + (item.price * item.quantity), 0) : 0)}</span>
              </div>
              <div className="total-line">
                <span>:</span>
                <span>{order.deliveryFee || order.deliveryCost || 0}</span>
              </div>
              {(order.discount || 0) > 0 && (
                <div className="total-line discount">
                  <span>:</span>
                  <span>-{order.discount}</span>
                </div>
              )}
              {order.promo && (
                <div className="total-line promo">
                  <span>:</span>
                  <span>{order.promo}</span>
                </div>
              )}
              <div className="total-line final">
                <span><strong>  :</strong></span>
                <span><strong>{order.total || order.payment?.total || 0}</strong></span>
              </div>
            </div>
          </div>

          {/*    */}
          <div className="order-section">
            <h4>   </h4>
            <div className="transaction-info">
              <div className="transaction-item">
                <strong> :</strong> {order.transaction?.totalAmount || order.payment?.total || order.total || 0}
              </div>
              <div className="transaction-item">
                <strong>  (10%):</strong> {order.transaction?.commission || order.payment?.commission || Math.round((order.total || 0) * 0.1)}
              </div>
              <div className="transaction-item">
                <strong>  :</strong> {order.transaction?.chefAmount || order.payment?.chefAmount || Math.round((order.total || 0) * 0.9)}
              </div>
              <div className="transaction-item">
                <strong> :</strong> {order.transaction?.status === 'reserved' ? '' : order.transaction?.status || ' '}
              </div>
              <div className="transaction-item">
                <strong> :</strong> {order.payment?.method || ' '}
              </div>
            </div>
          </div>

          {/*   */}
          <div className="order-section">
            <h4> </h4>
            <div className="chef-actions">
              {(order.status === 'pending' || order.status === 'pending_confirmation' || order.status === 'pending_payment') && (
                <>
                  <button 
                    onClick={() => handleStatusUpdate('confirmed')}
                    disabled={updating}
                    className="action-btn confirm-btn"
                  >
                      
                  </button>
                  <button 
                    onClick={() => handleStatusUpdate('cancelled')}
                    disabled={updating}
                    className="action-btn cancel-btn"
                  >
                      
                  </button>
                </>
              )}
              
              {order.status === 'confirmed' && (
                <button 
                  onClick={() => handleStatusUpdate('preparing')}
                  disabled={updating}
                  className="action-btn preparing-btn"
                >
                    
                </button>
              )}
              
              {order.status === 'preparing' && (
                <button 
                  onClick={() => handleStatusUpdate('ready')}
                  disabled={updating}
                  className="action-btn ready-btn"
                >
                     
                </button>
              )}
              
              {order.status === 'ready' && (
                <button 
                  onClick={() => handleStatusUpdate('delivering')}
                  disabled={updating}
                  className="action-btn delivering-btn"
                >
                     
                </button>
              )}
              
              {order.status === 'delivering' && (
                <button 
                  onClick={() => handleStatusUpdate('delivered')}
                  disabled={updating}
                  className="action-btn delivered-btn"
                >
                    
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ChefOrderDetails;
